.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 20px;
    @include onDesktop {
      margin-bottom: 32px;
    }
  }
  &__photo {
    width: 100%;
    grid-column: 2 /4;
    border-radius: 50%;
    transition: transform $duration;
    &:hover {
      transform: scale(1.3);
    }
  }
  &__name {
    margin-bottom: 4px;
    font-weight: bold;
    font-size: 24px;
    line-height: 125%;
    text-align: center;
  }
  &__role {
    margin-bottom: 4px;
    font-size: 14px;
    line-height: 193%;
    text-align: center;
    color: $colorGray;
  }
  &__about {
    text-align: center;
  }
}
